import { Link } from 'react-router-dom';
import ProgressBar from "@ramonak/react-progress-bar";
import './MyBooksDiv.css'
function MyBooksDiv() {
    return (
        <div id="MyBooksDiv" >
        

        <p id="MyBooksDiv-head" >Your Books</p> <div id="MyBooksDiv-body"> 
        <div className='bd'>
             <div className='imgg' id='t1' ></div> <p className='pp' >Automating stuff with python</p>  
             <ProgressBar 
    completed={40}
    bgColor="#ee41d6"
    height="10px"
    labelColor="#2c0339"
    labelSize="10px"
    baseBgColor="#31d8dc"
    animateOnRender
    maxCompleted={100}
    />
               </div>
        <div className='bd'> <div className='imgg' id='t2' ></div> <p className='pp' >Name</p> 
        
        <ProgressBar 
    completed={75}
    bgColor="#ee41d6"
    height="10px"
    labelColor="#2c0339"
    baseBgColor="#31d8dc"
    labelSize="10px"
    animateOnRender
    maxCompleted={100}
    />
        
        
        
         </div>
        
        <Link className='bd' to="/addbook"> Add </Link>  </div>  </div>
    );
  }
  
  export default MyBooksDiv;
